@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 3rem;

    @include m.tablet{
        flex-direction: row;
        justify-content: space-between;
    }
}

.navegacion{

    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .01s ease-in-out, opacity .5s linear, height .5s ease-in-out;

    @include m.tablet{
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
    }

    a{
        color: v.$blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;

        @include m.tablet{
            margin-right: 2rem;
            font-size: 1.8rem;
            
            &last-of-type{
                margin-right: 0;
            }
        }

    }
}

/** Menu de hamburguesa **/ 
.mobile-menu{
    @include m.tablet{
        display: none;
    }
    display: flex;

    img{
        width: 5rem;
    }
}

.mostrar{
    visibility: visible;
    opacity: 1;
    height: auto;
}
.dark-mode-boton{
    display: none;
}

@include m.tablet{
    .dark-mode-mvl{
        display: none;
    }
    .dark-mode-boton{
        display: block;
    }
}
.dark-mode-mvl{
    width: 3rem;
    filter: invert(100%);

    &:hover{
        cursor: pointer;
    }
}
.flex{
    display: flex;
    gap: 5rem;
}

.derecha{
    display: flex;

    .dark-mode-boton{
        width: 3rem;
        filter: invert(100%);

        &:hover{
            cursor: pointer;
        }
    }
}
