@use 'base/variables' as v;

.alerta{
    padding: .5rem;
    text-align: center;
    color: v.$blanco;
    font-weight: v.$bold;
    text-transform: uppercase;
    margin: 1rem 0;
    border-radius: 5rem;

    &.error{
        background-color: v.$rojo;
    }
    &.exito{
        background-color: v.$verde;
    }
}

table.propiedades {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    text-align: center;

    thead{
        background-color: v.$verde;

        th{
            color: v.$blanco;
            padding: 2rem;
        }
    }
    .imagen-tabla{
        width: 10rem;
    }
}

.imagen-small{
    width: 20rem;
}